import http from '../../../../configs/httpConfig'

/** Get user signup count by calender */
export const getUserSignedUpCalendar = () => {
  return async (dispatch) => {
    await http.get('/admin_dashboad/user_signed_up_count').then((response) => {
      dispatch({
        type: 'GET_USER_COUNT_CALENDAR',
        data: response.data.data
      })
    })
  }
}

