export const getDate = (from, to) => {
  return async (dispatch) => {
    await http.get(`/admin_dashboad/picker?from=${from} to=${to}`).then((response) => {
      dispatch({
        type: 'PICK_DATE',
        data: response.data.data
      })
    })
  }
}
